// Variables
$primaryColor: #4527a4;
$secondaryColor: #8344c5;
$loadingColor1: #e1f3ff;
$loadingColor2: #ebeeff;
$loadingColor3: #bbdefb;

// Columns
@for $i from 1 through 6 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}

	.cols-#{$i} {
		columns: #{$i};
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 6 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}

		.cols-tablet-#{$i} {
			columns: #{$i};
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 6 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}

		.cols-mobile-#{$i} {
			columns: #{$i};
		}
	}
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Inner Content
.apbInnerContent {
	h1 {
		font-size: 18px !important;
	}

	h2 {
		font-size: 17px !important;
	}

	h3 {
		font-size: 16px !important;
	}

	h4 {
		font-size: 15px !important;
	}

	h5 {
		font-size: 14px !important;
	}

	h6 {
		font-size: 14px !important;
	}

	p,
	span,
	div,
	a,
	button,
	mark {
		font-size: 15px !important;
	}

	.read-more,
	.wp-block-cover,
	.wp-block-image,
	img {
		display: none;
	}

	* {
		font-family: inherit !important;
	}
}

// Loading Placeholder
.apbLoadingPlaceholder {
	.apbLoadingItem {
		width: 100%;
		height: 60px;
		display: inline-block;
		background-color: $loadingColor1;
		border-radius: 3px;
		animation: bgBlink 1.5s linear infinite alternate;
	}

	.apbPost {
		border: 1px solid $loadingColor1;

		.apbThumb {
			position: relative;
		}

		.apbText {
			padding: 20px 15px;
		}

		.apbTitle,
		.apbMeta {
			margin-bottom: 15px;
		}

		.apbTitle {
			span {
				height: 25px;

				&:first-child {
					margin-bottom: 4px;
				}

				&:last-child {
					width: 80%;
				}
			}
		}

		.apbMeta {
			span {
				width: 95%;
				height: 14px;
			}
		}

		.apbExcerpt {
			display: grid;
			gap: 5px;
			margin-bottom: 10px;

			span {
				height: 15px;

				&:last-child {
					width: 60%;
				}
			}
		}

		.apbReadMore {
			height: 42px;

			span {
				width: 148px;
				height: 42px;
			}
		}
	}

	.apbGridPosts,
	.apbGrid1Posts {
		grid-gap: 15px 15px;
	}

	.apbSliderSkeleton {
		position: relative;

		.swiper-wrapper {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 15px 15px;

			@media (max-width: 768px) {
				grid-template-columns: 1fr;

				.swiper-slide:last-child {
					display: none;
				}
			}
		}

		.apbPost {
			display: grid;
			grid-template-columns: 1fr 1.3fr;
			align-items: center;

			.apbThumb {
				height: 100%;
			}
		}

		.swiper-button-prev,
		.swiper-button-next {
			color: $loadingColor3;
		}

		.swiper-pagination {
			width: 15px;
			height: 15px;
			border-radius: 50%;
			background-color: $loadingColor3;
			position: absolute;
			bottom: 10px;
			left: 50%;
			transform: translateX(-50%);

			&::before,
			&::after {
				content: '';
				width: 12px;
				height: 12px;
				border-radius: 50%;
				background-color: $loadingColor3;
				position: absolute;
				top: 2px;
			}

			&::before {
				left: -20px;
			}

			&::after {
				right: -20px;
			}
		}
	}

	.apbTickerPosts {
		display: flex;
		flex-direction: column;
		gap: 15px;

		.apbPost {
			display: grid;
			grid-template-columns: 1fr 1.3fr;

			.apbThumb {
				height: 207px;
				padding-bottom: 0;
			}

			.apbTitle {
				span:first-child {
					display: none;
				}
			}

			.apbExcerpt {

				span:nth-child(1),
				span:nth-child(2) {
					display: none;
				}
			}
		}
	}
}

@keyframes bgBlink {
	from {
		background-color: $loadingColor1;
	}

	to {
		background-color: $loadingColor2;
	}
}

// Advanced Posts
.wp-block-ap-block-posts {
	overflow: hidden;

	* {
		box-sizing: border-box;
	}

	.fillContentHeight {
		&.apbDefault {
			display: flex;
			flex-direction: column;

			.apbText {
				flex: 1;
				display: flex;
				flex-direction: column;
			}
		}

		&.apbSideImage {
			.apbText {
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
			}
		}
	}

	.apbGridPosts {
		display: grid;

		&::before,
		&::after {
			content: none;
		}

		.swiper-notification {
			display: none;
		}
	}

	.apbGrid1Posts {
		display: grid;
		grid-template-columns: repeat(4, 1fr);

		&::before,
		&::after {
			content: none;
		}

		.apbPost:nth-child(4n + 1) {
			grid-column: span 2;
			grid-row: span 2;
		}

		.apbPost:nth-child(4n + 2),
		.apbPost:nth-child(4n + 3) {
			grid-column: span 1;
			grid-row: span 1;
		}

		.apbPost:nth-child(4n + 4) {
			grid-column: span 2;
			grid-row: span 1;
		}

		@media (max-width: 992px) {
			grid-template-columns: repeat(2, 1fr);

			.apbPost:nth-child(3n + 1) {
				grid-column: span 1;
				grid-row: span 2;
			}

			.apbPost:nth-child(3n + 2),
			.apbPost:nth-child(3n + 3) {
				grid-column: span 1;
				grid-row: span 1;
			}
		}

		@media (max-width: 576px) {
			grid-template-columns: repeat(1, 1fr);

			.apbPost:nth-child(2n + 1),
			.apbPost:nth-child(2n + 2) {
				grid-column: span 1;
				grid-row: span 1;
			}
		}

		.swiper-notification {
			display: none;
		}
	}

	.apbPagination {
		display: flex;
		align-items: center;
		justify-content: center;
		list-style-type: none;
		padding: 0;
		margin: 15px 0 0 0;

		.apbPagePrev,
		.apbPageNumber,
		.apbPageNext {
			border-radius: 3px;
			cursor: pointer;

			&.apbActivePage {
				cursor: default;
			}
		}

		.disabled {
			display: none;
		}
	}
}

.apbSliderPosts {
	position: relative;
	overflow: hidden;
	height: auto;

	.swiper-wrapper {
		height: auto;
		align-items: center;
	}

	.swiper-pagination {
		.swiper-pagination-bullet:focus {
			outline: 0;
		}
	}

	.apbOverlay:not(.apbOverlayBox) .apbText {
		width: -webkit-fill-available !important;
	}

	article {
		box-sizing: border-box;
	}

	@media (max-width: 576px) {

		.swiper-button-prev,
		.swiper-rtl .swiper-button-next {
			left: 0;

			&::after {
				font-size: 30px;
			}
		}

		.swiper-button-next,
		.swiper-rtl .swiper-button-prev {
			right: 0;

			&::after {
				font-size: 30px;
			}
		}
	}
}

// .apbTickerPosts {
