// 		width: 100%;
// 	}
// }

.apbPost {
	border-radius: 5px;
	overflow: hidden;
	box-sizing: border-box;
	margin: 0;

	.apbMeta {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 10px;

		>span {
			display: inline-flex;
			align-items: center;
			gap: 10px;

			a {
				color: inherit;
			}
		}

		img {
			width: 1em;
			height: 1em;
			object-fit: contain;
		}

		.dashicons {
			width: auto;
			height: auto;
			font-size: inherit;
		}

		.dashicons-admin-comments {
			transform: translate(0, 2.5px);
		}
	}

	.apbExcerpt {
		p {
			margin: 0 !important;
			font-size: inherit !important;
		}
	}

	.apbReadMore {
		&.bottom {
			margin-top: auto;
		}

		a {
			display: inline-block;
			transition: all .3s ease;
		}
	}

	a {
		color: inherit;
		text-decoration: none;
		display: inline-block;
		transition: all .3s ease;
		cursor: pointer;
	}

	.apbThumb {
		position: relative;
		text-align: center;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		margin: 0;
		height: 10px;
		padding-bottom: 60%;

		>a,
		img {
			position: absolute;
			width: 100%;
			height: 100% !important;
			left: 0;
			top: 0;
		}

		.apbThumbCats {
			position: absolute;
			left: 10px;
			bottom: 10px;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 5px;

			a {
				text-decoration: none !important;
				padding: 2px 10px;
				border-radius: 2px;

				&:hover {
					cursor: pointer;
				}
			}
		}
	}


	&.apbSideImage {
		grid-template-columns: 1fr 1fr;
		align-items: center;
		height: inherit;
		display: flex;

		&.grid {
			display: grid;

			&::before,
			&::after {
				content: none;
			}
		}

		&.leftImage {
			grid-template-columns: 1fr 1.3fr;
		}

		&.rightImage {
			grid-template-columns: 1.3fr 1fr;
		}

		.apbThumb {
			padding-bottom: 0;
			height: 100% !important;
		}

		@media (max-width: 576px) {
			grid-template-columns: 1fr !important;
			height: auto !important;
			align-self: stretch !important;

			&.rightImage {
				.apbThumb {
					margin-top: auto;
				}
			}

			.apbThumb {
				width: 100%;
				height: 200px !important;
			}
		}
	}

	&.apbOverlay {
		position: relative;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		z-index: 0;

		&.apbOverlayHover {
			>div {
				opacity: 0;
				transition: all .3s ease-out !important;

				* {
					transition: all 0s ease !important;
					transition: color .3s ease !important;
				}
			}

			&:hover {
				>div {
					opacity: 1;
				}
			}
		}

		&.apbOverlayBox {
			padding: 10% 7.5%;

			&.swiper-slide {
				.apbText {
					padding-left: 0 !important;
					padding-right: 0 !important;
					width: 85% !important;
					min-height: auto;
					height: auto !important;
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
				}
			}
		}

		&.apbOverlayHalfContent {
			min-height: 400px;

			.apbText {
				justify-content: flex-end;
			}
		}

		img {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			z-index: -1;
		}

		.apbText {
			display: flex;
			flex-direction: column;
			justify-content: center;
			width: 100%;
			min-height: 100%;
		}
	}
}